








import { Component, Vue } from 'vue-property-decorator';
import MyDatePicker from '@/components/MyDatePicker.vue';
import ManPwrTracker from '@/components/ManPwrTracker.vue';
import { ManPwrArray } from '@/model/ManPwrArray';
import localForage from 'localforage';
import { ManPwrClass } from '../model/ManPwrClass';

@Component({
  components: {
    'my-date-picker': MyDatePicker,
    'man-pwr-tracker': ManPwrTracker,
  },
})
export default class Home extends Vue {
  private manpwrArray: ManPwrArray = new ManPwrArray();
  private currentDate: string = (new Date()).toDateString();
  private currentManpwr: ManPwrClass = new ManPwrClass();

  async created()
  {
    let temp: { [index: string]: ManPwrClass } = await localForage.getItem('manpwr');
    if(temp)
    {
      this.manpwrArray = new ManPwrArray(temp);
    }
    else
    {
      this.manpwrArray = new ManPwrArray();
    }
    this.currentManpwr = this.manpwrArray.getManPwr(this.currentDate);
  }

  private dateChanged(date: string)
  {
    this.currentDate = date;
    this.currentManpwr = this.manpwrArray.getManPwr(date);
  }

  private async updatedManPwr(input: ManPwrClass)
  {
    this.manpwrArray.setManpwr(this.currentDate, input);
    this.currentManpwr = this.manpwrArray.getManPwr(this.currentDate);
    await localForage.setItem('manpwr', this.manpwrArray.outputData);
  }
}
