








import { Component, Prop, PropSync, Emit, Vue } from 'vue-property-decorator';
import { ManPwrClass } from '@/model/ManPwrClass';

@Component
export default class ManPwrTracker extends Vue {
    @PropSync('manpwr') syncedManPwr!: ManPwrClass;
    @Emit('keyClicked')
    private keyClicked(key: string) {
        let output = {...this.syncedManPwr};
        output[key] = !output[key];
        this.syncedManPwr = output;
    }

    get keys(): string[] {
        return ["M", "A", "N", "P", "W", "R"];
    }
}
