import { ManPwrClass } from './ManPwrClass';

export class ManPwrArray
{
    private dict: { [index: string]: ManPwrClass } = {};

    public constructor(dict: { [index: string]: ManPwrClass } = {})
    {
        this.dict = dict;
    }

    private initializeElem(date: string)
    {
        if(this.dict[date] == undefined)
        {
            this.dict[date] = new ManPwrClass();
        }
    }

    public getManPwr(date: string)
    {
        this.initializeElem(date);
        return this.dict[date];
    }

    public get outputData()
    {
        return this.dict;
    }

    private getManPwrCountOnDate(date: string): number
    {
        this.initializeElem(date);
        return this.getManPwrCount(this.getManPwr(date));
    }

    private getManPwrCount(manpwr: ManPwrClass): number
    {
        let count = 0;
        if(manpwr)
        {
            if(manpwr.M) { count++; }
            if(manpwr.A) { count++; }
            if(manpwr.N) { count++; }
            if(manpwr.P) { count++; }
            if(manpwr.W) { count++; }
            if(manpwr.R) { count++; }
        }
        return count;
    }

    public toggleKey(date: string, key: string)
    {
        this.initializeElem(date);
        this.dict[date][key] = !this.dict[date][key];
    }

    private filter(skipDate: string)
    {
        let datesToFilter: string[] = [];
        for (var date in this.dict)
        {
            if((date != skipDate) && (this.getManPwrCountOnDate(date) == 0))
            {
                datesToFilter.push(date);
            }
        }
        datesToFilter.forEach((date) => {delete this.dict[date]});
    }

    public get consecutiveManPwrDays(): number
    {
        let curDate: Date = new Date();
        let output: number = 0;
        if(this.getManPwrCountOnDate(curDate.toDateString()) == 6)
        {
            output++;
        }
        curDate.setDate(curDate.getDate() - 1);
        while(this.getManPwrCountOnDate(curDate.toDateString()) == 6)
        {
            output++;
            curDate.setDate(curDate.getDate() - 1);
        }
        return output;
    }

    public setManpwr(date: string, input: ManPwrClass)
    {
        this.dict[date] = input;
        this.filter(date);
    }
}